import {Component, OnInit} from '@angular/core';
import {SessionService} from '../session.service';
import {HttpClient} from '@angular/common/http';

@Component({
  selector: 'app-user-settings',
  templateUrl: './user-settings.page.html',
  styleUrls: ['./user-settings.page.scss'],
})
export class UserSettingsPage implements OnInit {
  public person: { oldPassword: string, newPassword: string };
  public passwordConfirmation: string = null;

  constructor(private session: SessionService, private http: HttpClient) {
    this.person = {oldPassword: '', newPassword: ''};
  }

  ngOnInit() {
  }

  async submit() {
    if (!this.person.oldPassword) {
      return window.alert('Please type in your old password!');
    }
    if (this.person.newPassword !== this.passwordConfirmation) {
      return window.alert('Passwords don\'t match!');
    }
    console.log('token: ', this.session.token);
    const result = await this.http.post(this.session.apiUrl + 'api/Person/change-password?access_token=' + this.session.token.id, this.person).toPromise();
    console.log('result: ', result);
  }

}
